.TopNavBar {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    /* height: 20%;*/
    height: 192px;
    width: 100%;
    position: sticky;
    top: 0;
    background: url('https://raw.githubusercontent.com/prajeshkotian/Diagnal-LazyLoading-MovieApp/gh-pages/Assets/NavBar/nav_bar.png');
    background-size: cover;
    z-index: 1;
}

.Nav_item{
    
    margin-right: 30px;
    z-index:2;
    padding-bottom:30px;
}

.Nav_item:hover{
    transform:scale(1.1);
}

.Nav_item1{
    margin-left: 5px;
    margin-right: 5px;
    z-index:2;
   /* font-size: 40px;*/
    padding-bottom:30px;
}

.Nav_back_img{
    /*height: 192px;*/
    margin-left:30px;
    z-index: 2;
    padding-bottom:30px;
}

.Nav_back_img:hover{
    transform:scale(1.1);
}

.Nav_search {
    margin-left: auto;
    height: 20px;
    margin-bottom: 25px;
    width: 30%;
    padding: 16px 20px;
    border-radius: 25px;
    border: 0;
    outline: 0;
    font-size: 16px;
    background: #2d2f30;
    color: #f2f2f2;
    margin-right:10px;
}
